.card1 {
    /* border: 3px solid white; */
    margin: 15px;
    padding: 30px 45px;
    border-radius: 5px;
    background-color: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
    transform: rotate(0deg);
    transition: transform 0.2s ease; /* Add a transition for smooth animation */
}

.card1:hover {
    animation: rotateBack 0.600s forwards;
    /* Apply the animation on hover */
}

@keyframes rotateBack1 {
    0% {
        transform: rotate(0deg);
        /* Rotate the card by 30 degrees */
    }
    10% {
        transform: rotate(10deg);
        /* Rotate the card by 30 degrees */
    }
    20% {
        transform: rotate(25deg);
        /* Rotate the card by 30 degrees */
    }
    30% {
        transform: rotate(10deg);
        /* Rotate the card by 30 degrees */
    }
    50% {
        transform: rotate(20deg);
        /* Rotate the card by 30 degrees */
    }
    60% {
        transform: rotate(10deg);
        /* Rotate the card by 30 degrees */
    }
    70% {
        transform: rotate(0deg);
        /* Rotate the card by 30 degrees */
    }
    80% {
        transform: rotate(-10deg);
        /* Rotate the card by 30 degrees */
    }
    90% {
        transform: rotate(-20deg);
        /* Rotate the card by 30 degrees */
    }

    100% {
        transform: rotate(-30deg);
        /* Return the card to its original position */
    }
}

@keyframes rotateBack{
    0% {
        transform: rotate(0deg);
        /* Rotate the card by 30 degrees */
    }
    25% {
        transform: rotate(10deg);
        /* Rotate the card by 30 degrees */
    }
    50% {
        transform: rotate(0deg);
        /* Rotate the card by 30 degrees */
    }

    75% {
        transform: rotate(-10deg);
        /* Rotate the card by 30 degrees */
    }
    100% {
        transform: rotate(0deg);
        /* Rotate the card by 30 degrees */
    }

  
}

